import { useState } from 'react';
import { 
  Box, 
  IconButton, 
  Drawer, 
  MenuItem, 
  Divider, 
  Button 
} from '@mui/material';
import { Link } from 'react-router-dom';
import MenuIcon from '@mui/icons-material/Menu';
import CloseRoundedIcon from '@mui/icons-material/CloseRounded';
import LanguageSwitchComponent from './LanguageSwitch';
import ThemeSwitch from './ThemeSwitch';
import { translations } from './translations';
import { useUser } from '../../../context/UserContext';
import UserService from '../../../services/UserService';
import PasswordResetModal from './PasswordResetModal'; // Import the PasswordResetModal component

const MobileDrawer = ({ 
  language, 
  toggleLanguage, 
  toggleTheme, 
  handleLoginOpen, 
  handleSignupOpen 
}) => {
  const [open, setOpen] = useState(false);
  const [passwordResetOpen, setPasswordResetOpen] = useState(false); // State for password reset modal
  const { userState, setUser } = useUser();

  const toggleDrawer = (newOpen) => () => {
    setOpen(newOpen);
  };

  const handleLogout = async () => {
    const userData = await UserService.getNonuserJWT();
    setUser(userData);
    setOpen(false);
  };

  // Handle opening the password reset modal
  const handlePasswordResetOpen = () => {
    setPasswordResetOpen(true);
    setOpen(false); // Optionally close the drawer when opening the modal
  };

  return (
    <Box sx={{ display: { xs: 'flex', md: 'none' }, gap: 1 }}>
      <IconButton aria-label="Menu button" onClick={toggleDrawer(true)}>
        <MenuIcon />
      </IconButton>
      <Drawer
        anchor="top"
        open={open}
        onClose={toggleDrawer(false)}
        PaperProps={{
          sx: { top: 'var(--template-frame-height, 0px)' },
        }}
      >
        <Box sx={{ p: 2, backgroundColor: 'background.default' }}>
          <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
            <IconButton onClick={toggleDrawer(false)}>
              <CloseRoundedIcon />
            </IconButton>
          </Box>
          
          <MenuItem component={Link} to="/">{translations[language].home}</MenuItem>
          <MenuItem component={Link} to="/notes">{translations[language].notes}</MenuItem>
          <MenuItem component={Link} to="/blogs">{translations[language].blogs}</MenuItem>
          <MenuItem component={Link} to="/projects">{translations[language].projects}</MenuItem>
          <MenuItem component={Link} to="/fin">{translations[language].finance}</MenuItem>
          <MenuItem component={Link} to="/profile">{translations[language].profile}</MenuItem>
          {/* <MenuItem component={Link} to="/dashboard">{translations[language].dashboard}</MenuItem> */}
          
          <Divider sx={{ my: 3 }} />
          
          <MenuItem>
            <LanguageSwitchComponent onClick={toggleLanguage} />
          </MenuItem>
          <MenuItem>
            <ThemeSwitch onClick={toggleTheme} />
          </MenuItem>
          
          <Divider sx={{ my: 3 }} />

          {userState.user_id === 0 ? (
            <>
              <MenuItem>
                <Button
                  color="primary"
                  variant="outlined"
                  fullWidth
                  onClick={handleLoginOpen}
                >
                  {translations[language].login}
                </Button>
              </MenuItem>
              <MenuItem>
                <Button
                  color="primary"
                  variant="contained"
                  fullWidth
                  onClick={handleSignupOpen}
                >
                  {translations[language].signup}
                </Button>
              </MenuItem>
            </>
          ) : (
            <>
              <MenuItem>
                <Button
                  color="primary"
                  variant="outlined"
                  fullWidth
                  onClick={handlePasswordResetOpen} // Updated to use the new handler
                >
                  {translations[language].password_reset}
                </Button>
              </MenuItem>
              <MenuItem>
                <Button
                  color="primary"
                  variant="contained"
                  fullWidth
                  onClick={handleLogout}
                >
                  {translations[language].logout}
                </Button>
              </MenuItem>
              <Divider sx={{ my: 1 }} />
              <MenuItem>
                <Button
                  color="info"
                  fullWidth
                  variant="text"
                  disabled
                  sx={{ textTransform: 'none' }}
                >
                  {userState.username}
                </Button>
              </MenuItem>
            </>
          )}
        </Box>
      </Drawer>

      {/* Password Reset Modal */}
      <PasswordResetModal
        open={passwordResetOpen}
        onClose={() => setPasswordResetOpen(false)}
        language={language}
      />
    </Box>
  );
};

export default MobileDrawer;