import { useState } from 'react';
import { Button, Menu, MenuItem } from '@mui/material';
import { translations } from './translations';
import { useUser } from '../../../context/UserContext';
import UserService from '../../../services/UserService';
import PasswordResetModal from './PasswordResetModal'; // Import the new component

const UserMenu = ({ language }) => {
  const { userState, setUser } = useUser();
  const [anchorEl, setAnchorEl] = useState(null);
  const [passwordResetOpen, setPasswordResetOpen] = useState(false); // State to control modal visibility
    
  const handleLogout = async () => {
    const userData = await UserService.getNonuserJWT();
    setUser(userData);
    setAnchorEl(null);
  };

  const handlePasswordResetClick = () => {
    setAnchorEl(null); // Close the menu
    setPasswordResetOpen(true); // Open the password reset modal
  };

  return (
    <>
      <Button
        variant="text"
        color="info"
        size="small"
        onClick={(e) => setAnchorEl(e.currentTarget)}
        sx={{ textTransform: 'none' }}
      >
        {userState.username}
      </Button>
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={() => setAnchorEl(null)}
        sx={{ ml: 0 }}
      >
        <MenuItem onClick={handlePasswordResetClick} sx={{ fontSize: '0.85rem' }}>
          {translations[language].password_reset || "Reset Password"}
        </MenuItem>
        <MenuItem onClick={handleLogout} sx={{ fontSize: '0.85rem' }}>
          {translations[language].logout || 'Log out'}
        </MenuItem>
      </Menu>

      {/* Password Reset Modal */}
      <PasswordResetModal
        open={passwordResetOpen}
        onClose={() => setPasswordResetOpen(false)}
        language={language}
      />
    </>
  );
};

export default UserMenu;