import { Modal, Box, Backdrop } from '@mui/material';
import LoginForm from './LoginForm';
import SignupForm from './SignupForm';

export const LoginModal = ({ open, handleClose, handleSignupOpen }) => {
  const handleSignupClick = () => {
    handleClose();
    handleSignupOpen();
  };

  return (
    <Modal
      open={open}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
        style: { backgroundColor: 'rgba(0, 0, 0, 0.5)', backdropFilter: 'blur(5px)' },
      }}
    >
      <Box
        sx={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: '100%',
          maxWidth: 450,
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <LoginForm
          onClose={handleClose}
          onSignupClick={handleSignupClick}
        />
      </Box>
    </Modal>
  );
};

export const SignupModal = ({ open, handleClose, handleLoginOpen }) => {
  const handleLoginClick = () => {
    handleClose();
    handleLoginOpen();
  };

  return (
    <Modal
      open={open}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
        style: { backgroundColor: 'rgba(0, 0, 0, 0.5)', backdropFilter: 'blur(5px)' },
      }}
    >
      <Box
        sx={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: { md: '100%', xs: "80%" },
          maxWidth: 450,
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <SignupForm
          onClose={handleClose}
          onLoginClick={handleLoginClick}
        />
      </Box>
    </Modal>
  );
};