import { useState } from 'react';
import { 
  Dialog, 
  DialogTitle, 
  DialogContent, 
  DialogActions, 
  TextField, 
  Button, 
  Box, 
  CircularProgress, 
  Alert
} from '@mui/material';
import UserService from '../../../services/UserService';
import { useUser } from '../../../context/UserContext';
import { translations } from './translations';

const PasswordResetModal = ({ open, onClose, language }) => {
  const { userState, setUser } = useUser();
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState(false);
  
  const isSuperuser = userState.is_superuser;
  
  const resetForm = () => {
    setCurrentPassword('');
    setNewPassword('');
    setConfirmPassword('');
    setError('');
    setSuccess(false);
  };
  
  const handleClose = () => {
    resetForm();
    onClose();
  };
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess(false);
    
    // Validation
    if (newPassword !== confirmPassword) {
      setError(translations[language].passwords_not_match || 'New passwords do not match');
      return;
    }
    
    if (newPassword.length < 6) {
      setError(translations[language].password_too_short || 'Password must be at least 6 characters');
      return;
    }
    
    setLoading(true);
    
    try {
      // If superuser, currentPassword is optional
      const result = await UserService.reset_password(
        userState.user_id, 
        newPassword,
        !isSuperuser ? currentPassword : null, // Only send currentPassword if not superuser
        userState.access
      );
      
      if (result.success) {
        setSuccess(true);
        // Reset form fields after successful password change
        setCurrentPassword('');
        setNewPassword('');
        setConfirmPassword('');

        // Logout and Reset user state
        const userData = await UserService.getNonuserJWT();
        setUser(userData);
        
        // Auto close after 1 seconds on success
        setTimeout(() => {
          handleClose();
        }, 1000);
      } else {
        setError(result.message || translations[language].password_reset_failed || 'Failed to reset password');
      }
    } catch (err) {
      setError(translations[language].password_reset_error || 'An error occurred during password reset');
      console.error('Password reset error:', err);
    } finally {
      setLoading(false);
    }
  };
  
  return (
    <Dialog open={open} onClose={loading ? undefined : handleClose} maxWidth="sm" fullWidth>
      <DialogTitle>
        {translations[language].password_reset || 'Reset Password'}
      </DialogTitle>
      <form onSubmit={handleSubmit}>
        <DialogContent>
          {error && <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>}
          {success && <Alert severity="success" sx={{ mb: 2 }}>
            {translations[language].password_reset_success || 'Password reset successfully!'}
          </Alert>}
          
          {/* Current password field (only required for non-superusers) */}
          {!isSuperuser && (
            <TextField
              margin="dense"
              label={translations[language].current_password || 'Current Password'}
              type="password"
              fullWidth
              variant="outlined"
              value={currentPassword}
              onChange={(e) => setCurrentPassword(e.target.value)}
              required
              disabled={loading || success}
              sx={{ mb: 2 }}
            />
          )}
          
          {/* New password fields */}
          <TextField
            margin="dense"
            label={translations[language].new_password || 'New Password'}
            type="password"
            fullWidth
            variant="outlined"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            required
            disabled={loading || success}
            sx={{ mb: 2 }}
          />
          
          <TextField
            margin="dense"
            label={translations[language].confirm_new_password || 'Confirm New Password'}
            type="password"
            fullWidth
            variant="outlined"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            required
            disabled={loading || success}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} disabled={loading}>
            {translations[language].cancel || 'Cancel'}
          </Button>
          <Box sx={{ position: 'relative' }}>
            <Button 
              type="submit" 
              color="primary" 
              variant="contained" 
              disabled={loading || success}
            >
              {translations[language].reset || 'Reset'}
            </Button>
            {loading && (
              <CircularProgress
                size={24}
                sx={{
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  marginTop: '-12px',
                  marginLeft: '-12px',
                }}
              />
            )}
          </Box>
        </DialogActions>
      </form>
    </Dialog>
  );
};

export default PasswordResetModal;