import { useState, useContext, useEffect } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormLabel from '@mui/material/FormLabel';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import MuiCard from '@mui/material/Card';
import CircularProgress from '@mui/material/CircularProgress';
import { styled } from '@mui/material/styles';
import { SettingsContext } from '../../../context/SettingsContext';
import UserService from '../../../services/UserService';
import { useUser } from '../../../context/UserContext'; 
import CloseRoundedIcon from '@mui/icons-material/CloseRounded';
import IconButton from '@mui/material/IconButton';

const Card = styled(MuiCard)(({ theme, themeMode }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignSelf: 'center',
  padding: '32px',
  margin: 'auto',
  borderRadius: '8px',
  gap: '16px',
  boxShadow: themeMode === 'dark'
    ? '0px 4px 20px rgba(0, 0, 0, 0.5)'
    : '0px 4px 20px rgba(0, 0, 0, 0.2)',
  [theme.breakpoints.down('sm')]: {
    padding: '24px',
    gap: '8px',
  },
}));

export default function LoginForm({ onClose, onSignupClick }) {
  const { language, themeMode } = useContext(SettingsContext);
  const { userState, setUser } = useUser();

  const text = {
    en: {
      login: 'Log in',
      username: 'Username',
      usernamePlaceholder: 'username',
      password: 'Password',
      passwordPlaceholder: '••••••',
      rememberMe: 'Remember me',
      loginButton: 'Log In',
      cancelButton: 'Cancel',
      dividerText: 'or',
      noAccount: "Don't have an account?",
      signUp: 'Sign up',
      usernameError: 'Username is required',
      passwordError: 'Password is required',
      unexpectedError: 'An unexpected error occurred',
    },
    cn: {
      login: '登录',
      username: '用户名',
      usernamePlaceholder: '用户名',
      password: '密码',
      passwordPlaceholder: '••••••',
      rememberMe: '记住我的密码',
      loginButton: '登录',
      cancelButton: '取消',
      dividerText: '或',
      noAccount: '还没有账号？',
      signUp: '注册',
      usernameError: '用户名是必填项',
      passwordError: '密码是必填项',
      unexpectedError: '意外错误',
    },
  };

  const t = text[language] || text.en;

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [rememberMe, setRememberMe] = useState(false);
  const [usernameError, setUsernameError] = useState(false);
  const [usernameErrorMessage, setUsernameErrorMessage] = useState('');
  const [passwordError, setPasswordError] = useState(false);
  const [passwordErrorMessage, setPasswordErrorMessage] = useState('');
  const [loading, setLoading] = useState(false);

  // 当组件加载时，检查是否有保存的凭据
  useEffect(() => {
    const savedUsername = localStorage.getItem('rememberedUsername');
    const savedPassword = localStorage.getItem('rememberedPassword');
    
    if (savedUsername && savedPassword) {
      setUsername(savedUsername);
      setPassword(savedPassword);
      setRememberMe(true);
    }
  }, []);

  const handleSubmit = async (event) => {
    event.preventDefault();
    setUsernameError(false);
    setPasswordError(false);
    setUsernameErrorMessage('');
    setPasswordErrorMessage('');
    setLoading(true);
  
    if (!username) {
      setUsernameError(true);
      setUsernameErrorMessage(t.usernameError);
      setLoading(false);
      return;
    }
  
    if (!password) {
      setPasswordError(true);
      setPasswordErrorMessage(t.passwordError);
      setLoading(false);
      return;
    }
  
    try {
      const response = await UserService.login(username, password, userState.access);
      if (response.status === 200) {
        // 处理"记住我"功能
        if (rememberMe) {
          localStorage.setItem('rememberedUsername', username);
        } else {
          localStorage.removeItem('rememberedUsername');
        }
        
        setUser(response);
        if (onClose) onClose();
      }
    } catch (error) {
      console.error('Login failed:', error);
      setPasswordError(true);
      setPasswordErrorMessage(
        error.response?.data?.detail || t.unexpectedError
      );
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        padding: 2,
      }}
    >
      <Card
        component="form"
        onSubmit={handleSubmit}
        sx={{
          width: {
            xs: '90%',
            md: '100%',
          },
          maxWidth: '450px',
        }}
        themeMode={themeMode}
      >
        <IconButton
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: {
              xs: 35,
              md: 20,
            },
            top: {
              xs: 15,
              md: 20,
            },
          }}
        >
          <CloseRoundedIcon />
        </IconButton>
        <Typography
          component="h1"
          variant="h4"
          align="center"
          sx={{
            fontWeight: 'bold',
            fontSize: {
              xs: '1.5rem',
              md: '1.8rem',
            },
            color: 'primary.main',
            letterSpacing: 0.8,
          }}
        >
          {t.login}
        </Typography>
        <FormControl>
          <FormLabel htmlFor="username">{t.username}</FormLabel>
          <TextField
            error={usernameError}
            helperText={usernameErrorMessage}
            id="username"
            type="text"
            name="username"
            placeholder={t.usernamePlaceholder}
            autoComplete="username"
            fullWidth
            variant="outlined"
            required
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </FormControl>
        <FormControl>
          <FormLabel htmlFor="password">{t.password}</FormLabel>
          <TextField
            error={passwordError}
            helperText={passwordErrorMessage}
            id="password"
            type="password"
            name="password"
            placeholder={t.passwordPlaceholder}
            autoComplete="current-password"
            fullWidth
            variant="outlined"
            required
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </FormControl>
        <FormControlLabel
          control={
            <Checkbox 
              value="remember" 
              color="primary" 
              checked={rememberMe}
              onChange={(e) => setRememberMe(e.target.checked)}
            />
          }
          label={
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              {t.rememberMe}
              {loading && (
                <CircularProgress
                  size={20}
                  sx={{
                    marginLeft: 2,
                  }}
                />
              )}
            </Box>
          }
        />

        <Button type="submit" fullWidth variant="contained" color="primary">
          {t.loginButton}
        </Button>
        <Divider>{t.dividerText}</Divider>
        <Typography align="center" variant="body2">
          {t.noAccount}{' '}
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              if (onSignupClick) onSignupClick();
            }}
          >
            {t.signUp}
          </a>
        </Typography>
      </Card>
    </Box>
  );
}